import React from 'react';
import workspace from '../images/workspace.png';
import icon1 from '../images/icon1.png';
import icon2 from '../images/icon2.png';
import icon3 from '../images/icon3.png';
import icon4 from '../images/icon4.png';
import icon5 from '../images/icon5.png';

import {Link} from 'react-router-dom';

/* Used Bootstrap 4 Flex for this. See w3schools.com/bootstrap4/bootstrap_flex.asp
   Screen Sizes: xs (<=576px), sm (>=576px), md (>=768px), lg (>=992px) or xl (>=1200px))
*/

function MiddleSection() {

    return (
        <section className="middlesection" id="projects">
            <h2>Projects</h2>
            <img className="img-fluid" src={workspace} alt="" width="300" class="mx-auto d-block rounded" />
            <div className="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4 row-cols-xl-5">
                <div className="col mb-4 mt-4">
                    <div className="card h-100">
                        <img className="card-img-top" src={icon1} alt="" />
                        <div className="card-body">
                            <h3 className="card-title">React</h3>
                            <p className="card-text">I created this math game using React. Click the button to test your math skills.</p>
                            <Link className="btn btn-secondary" to="/math">Play Math!</Link>
                        </div>
                    </div>
                </div>
                <div className="col mb-4 mt-4">
                    <div className="card h-100">
                        <img className="card-img-top" src={icon2} alt="" />
                        <div className="card-body">
                            <h3 className="card-title">C++</h3>
                            <p className="card-text">I worked at Autodesk, designing and developing new features for multiple releases of AutoCAD and AutoCAD LT. </p>
                        </div>
                    </div>
                </div>
                <div className="col mb-4 mt-4">
                    <div className="card h-100">
                        <img className="card-img-top" src={icon3} alt="" />
                        <div className="card-body">
                            <h3 className="card-title">Python</h3>
                            <p className="card-text">I coded this Connect 4 game using Python. Click the button to play.</p>
                            <Link className="btn btn-secondary" to="/">Coming Soon</Link>
                        </div>
                    </div>
                </div>
                <div className="col mb-4 mt-4">
                    <div className="card h-100">
                        <img className="card-img-top" src={icon4} alt=""></img>
                        <div className="card-body">
                            <h3 className="card-title">My Favorite Online Game</h3>
                            <p className="card-text">If you like Soduko, try these challenging puzzles.</p>
                            <a href="https://www.youtube.com/channel/UCC-UOdK8-mIjxBQm_ot1T-Q" className="btn btn-secondary" role="button" rel="noopener noreferrer" target="_blank">Sudoku</a>
                        </div>
                    </div>
                </div>
                <div className="col mb-4 mt-4">
                    <div className="card h-100">
                        <img className="card-img-top" src={icon5} alt=""></img>
                        <div className="card-body">
                            <h3 className="card-title">My Creative Outlet</h3>
                            <p className="card-text">Etsy keeps me well rounded.</p>
                            <a href="https://www.etsy.com/shop/LemonTreeStitches?ref=seller-platform-mcnav" className="btn btn-secondary" role="button" rel="noopener noreferrer" target="_blank">Etsy</a>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default MiddleSection;