import React from 'react';
import SelectInput from './SelectInput';
import PlayButton from './PlayButton';
import './Main.css';

// operations and values are surrounded by {} because they are JavaScript not JSX
// The row classes break the lines into rows and add horizontal and vertical margins.
function Main(props) {
    
    const operations = [['Addition', '+'],
                        ['Subtraction',  '-'],
                        ['Multiplication', 'x'],
                        ['Division', '/']];
    //Fill array with numbers from 2 to 100
    const numbers = [];
    for (let i = 2; i <= 100; i++) {
        numbers.push([i, i]);
    }
    return(
        <main>
            <h1>Play Math!</h1>
            <div className="row mx-1 my-3">
                <SelectInput label="Operation"
                    id="operation"
                    currentValue={props.operation}
                    setCurrentValue={props.setOperation}
                    values={operations}/>
            </div>
            <div className="row mx-1 my-3">
                <SelectInput label="Maximum Number"
                    id="max-number"
                    currentValue={props.maxNumber}
                    setCurrentValue={props.setMaxNumber}
                    values={numbers}/>
            </div>
            <div className="row mx-1 my-3">
            <PlayButton />
            </div>
        </main>
    )
}
export default Main;