import React from 'react';

/* When the page is short, make sure the footer is fixed to the bottom. */

function Footer(props) {

  var styleType;
  
  if (props.sticky === true)
    styleType = "navbar fixed-bottom";
  else
    styleType = "navbar";

  return (
    <footer className={styleType}>
      <span className="text-light"> Copyright &copy; {new Date().getFullYear()} </span>
    </footer>
  )
}

export default Footer;
