import React, {useEffect} from 'react';

// Destructure props parameter.
function Timer({timeLeft, setTimeLeft}) {

    useEffect(() => {
        // Mounting
        // use setTimeout() to decrement timeLeft by 1 every
        // 1000 milliseconds (1 second) until timeLeft is 0
        const timer = setInterval(() => {
            setTimeLeft(prevTimeLeft => prevTimeLeft - 1);
        }, 1000);
        return () => {
            // Unmounting
            clearInterval(timer); //Cleanup the timer
        }
        // Adding this weird comment disables a compiler warning
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []); // Dependencies array is empty, so code will run only when the
            // Timer mounts and unmounts.

    return (
        <strong>Time: {timeLeft}</strong>
    )
}

export default Timer;