import React, {useState} from 'react';
import './App.css';
//import new components use ./ before the filename and don't include .js
import Header from '../components/Header';
import Main from '../components/Main';
import Footer from '../components/Footer';
import Game from '../components/Game';
import {Route, Switch} from 'react-router-dom';
import TopSection from './TopSection';
import MiddleSection from './MiddleSection';
import BottomSection from './BottomSection';


function App() {

  /* Define State variables and pass to the main component */
  const [operation, setOperation] = useState('x');
  const [maxNumber, setMaxNumber] = useState(10);

  return (
    <div className="App">
      <Header />
      <Switch>
        <Route path="/math/play">
          <Game operation={operation}
            maxNumber={maxNumber} />
          <Footer sticky={true} />
        </Route>
        <Route path="/math">
          <Main operation={operation}
            setOperation={setOperation}
            maxNumber={maxNumber}
            setMaxNumber={setMaxNumber} />
          <Footer sticky={true} />
        </Route>
        <Route path="/">
          <TopSection />
          <MiddleSection />
          <BottomSection />
          <Footer sticky={false}/>
        </Route>
      </Switch>
    </div>
  );
}

export default App;
