import React from 'react';
import workspace03 from '../images/workspace03.jpg';
import tealhex from '../images/tealhex.png';
import bluehex from '../images/bluehex.png';
import greenhex from '../images/greenhex.png';


function TopSection() {
    return (
        <section className="topsection" id="skills">
            <img className="img-fluid" src={workspace03} alt="" width="800" class="mx-auto d-block"/>
            <h2>Hi, I'm Karry Saunders</h2>
            <h4>Welcome to my website!</h4>
            <div className="skillslist">
            <div className="d-flex p-3 flex-row flex-wrap justify-content-around">
                <div className="p-2">
                <img className="img-fluid" src={greenhex} alt="" width="100" height="125" />
                <h3>Programming</h3>
                <ul>
                    <li>Python</li>
                    <li>C++</li>
                    <li>C</li>
                    <li>Visual Studio</li>
                </ul>
                </div>
                <div className="p-2">
                <img className="img-fluid" src={bluehex} alt="" width="100" height="125" />
                <h3>Web Development</h3>
                <ul>
                    <li>HTML/CSS</li>
                    <li>JavaScript</li>
                    <li>JQuery</li>
                    <li>Bootstrap</li>
                    <li>React</li>
                </ul>
                </div>
                <div className="p-2">
                <img className="img-fluid" src={tealhex} alt="" width="100" height="125" />
                <h3>Work Ethic</h3>
                <ul>
                    <li>Dedicated</li>
                    <li>Detail-oriented</li>
                    <li>Hardworking</li>
                    <li>Respectful</li>
                    <li>Team Player</li>
                </ul>
                </div>
            </div>
        </div>
        </section>
    )
}
  
export default TopSection;