import React, {Fragment} from 'react';

/* A fragment creates a container around JSX elements without producing any HTML output.
   This makes one component in the return statement. Be sure to import {Fragment}. */
   
function Equation(props) {
    return (
        <Fragment>
            <div className="col-5">{props.question}</div>
            <div className="col-2">=</div>
            <div className="col-5">{props.answer}</div>
        </Fragment>
    )
}
export default Equation;