import React from 'react';

/* Use an inline style to create a bigger clear button.
   There's only one clear button so it's overkill to
   create a ClearButton.css for just one button.
   Instead we use an inline style. */

function ClearButton(props) {
    return (
        <button className="btn btn-primary" style={{width: "4.2em"}}
        onClick={() => {props.handleClick('');}}>Clear</button>
    )
}
export default ClearButton;